function Timer() {
    var obj = {};

    obj.onTimer = function (e,type) {
        console.log(e+"----"+type);
    };

    obj.start = function (duration, time) {
        obj.timeout = setTimeout(() => {
            duration -= time;
            if (duration > 0) {
                obj.start(duration, time);
                obj.onTimer(duration, 'timer');
            } else {
                obj.onTimer(-1, 'stop');
                obj.stop();
            }
        }, time * 1000)
    };

    obj.stop = function () {
        if (obj.timeout != null) {
            clearTimeout(obj.timeout);
        }
        obj.timeout = null;
    };
    return obj;
}
module.exports = Timer;