function keyOfFun(hotKyes, key) {
    for (var i = 0; i < hotKyes.length; i++) {
        if (hotKyes[i] == key) {
            switch (i) {
                case 0:
                    return 'payPage';
                case 1:
                    return 'orderPage';
                case 3:
                    return 'barcode';
                case 4:
                    return 'shift';
                case 7:
                    return 'passwordPage';
                case 8:
                    return 'hotkeyPage';
                case 9:
                    return 'scan';
                case 11:
                    return 'quit';
            }
        }
    }
    return '';
}
export default keyOfFun;