import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    keys: ['F1', 'F2', 'F3', 'F4', 'F5', 'F6', 'F7', 'F8', 'F9', 'F10', 'F11', 'F12'],
    focus: false
  },
  getters: {
    getKeys: function (state) {
      return state.keys;
    },
    isFocus: function (state) {
      return state.focus;
    }
  },
  mutations: {
    setKeys: function (state, keys) {
      state.keys = keys;
    },
    setFocus: function (state, focus) {
      state.focus = focus;
    }
  },
  actions: {
    setHotKeys: function ({ commit, state }, keys) {
      this.commit('setKeys', keys);
    },
    changeFocus: function ({ commit, state }, focus) {
      this.commit('setFocus', focus);
    }
  }
})