<template>
  <div class="zzw-layout-content login-page">
    <form class="zzw-form form-block">
      <div class="zzw-form-title">
        <img src="../../static/img/logo.png" alt id="logo" />
        <p>商家助手</p>
      </div>
      <div class="zzw-form-group">
        <i class="fa fa-user"></i>
        <input
          id="faccount"
          type="text"
          class="zzw-input"
          maxlength="11"
          placeholder="账号"
          v-model="account"
        />
      </div>
      <div class="zzw-form-group zzw-form-pwd">
        <i class="fa fa-lock"></i>
        <input
          id="fpasswd"
          type="password"
          class="zzw-input"
          maxlength="20"
          placeholder="密码"
          v-model="password"
        />
      </div>
      <div class="zzw-form-group">
        <div class="zzw-checkbox primary">
          <input type="checkbox" id="rmbPSD" />
          <label onclick="checkbox(this)" for="rmbPSD">自动登录</label>
          <i class="fa fa-check"></i>
        </div>
      </div>
      <div class="zzw-form-group">
        <a @click="login" class="zzw-btn">登录</a>
      </div>
    </form>
  </div>
</template>

<script>
import md5 from "md5";
export default {
  name: "Login",
  data() {
    return {
      account: "",
      password: "",
      remember: false
    };
  },
  created:function(){
    this.account='0000215'
    this.password='12345678'
  },
  methods: {
    login: function() {
      var from = new FormData();
      from.append("faccount", this.account);
      from.append("fpasswd", md5(this.password));
      from.append("fversion", "2.0");
      this.$axios
        .post("/api/login", from)
        .then(result => {
          var data=result.data;
          if(data.code=='0000'){
            this.$router.replace('home');
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>
