<template>
  <!--内容-->
  <div class="zzw-body">
    <div class="zzw-card-body">
      <form class="zzw-form-list flex" style="width: 50%;margin: 0 auto;" id="setForm">
        <div class="zzw-form-item">
          <div class="zzw-form-label">旧的密码</div>
          <div class="zzw-flex-box">
            <input
              name="oldpwd"
              id="oldpwd"
              type="password"
              class="zzw-input"
              v-model="oldPwd"
              @blur="changeFocus(false)"
              @focus="changeFocus(true)"
            />
          </div>
        </div>
        <div class="zzw-form-item">
          <div class="zzw-form-label">新的密码</div>
          <div class="zzw-flex-box">
            <input
              name="newpwd"
              id="newpwd"
              type="password"
              class="zzw-input"
              v-model="newPwd"
              @blur="changeFocus(false)"
              @focus="changeFocus(true)"
            />
          </div>
        </div>
        <div class="zzw-form-item">
          <div class="zzw-form-label">确认密码</div>
          <div class="zzw-flex-box">
            <input
              name="repwd"
              id="repwd"
              type="password"
              class="zzw-input"
              v-model="aginPwd"
              @blur="changeFocus(false)"
              @focus="changeFocus(true)"
            />
          </div>
        </div>
        <div class="zzw-form-group mb0">
          <div class="zzw-btn-block tc">
            <a class="zzw-btn btn-fluid" @click="password">保存</a>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import md5 from "md5";

export default {
  data() {
    return {
      oldPwd: "",
      newPwd: "",
      aginPwd: ""
    };
  },
  destroyed: function() {
    this.$store.dispatch("changeFocus", false);
  },
  methods: {
    changeFocus: function(focus) {
      this.$store.dispatch("changeFocus", focus);
    },
    password: function() {
      if (this.oldPwd == "") {
        return;
      }

      if (this.newPwd == "") {
        return;
      }

      if (this.newPwd.length < 8) {
        return;
      }

      if (this.newPwd != this.aginPwd) {
        return;
      }
      var data = new FormData();
      data.append("oldpwd", md5(this.oldPwd));
      data.append("newpwd", md5(this.newPwd));
      this.$axios
        .post("/api/user/password", data)
        .then(result => {
          var data = result.data;
          if (data.code == "0000") {
            alert("密码修改成功");
            this.$router.replace("/");
          } else {
            alert(data.msg);
          }
        })
        .catch(err => {});
    }
  }
};
</script>