<template>
  <!--页面内容-->
  <div v-bind:class="'zzw-layout-content index-page '+(menuVisible?'':'zzw-side-shrink')">
    <!--头部-->
    <header class="zzw-header" id="header">
      <ul class="zzw-nav zzw-layout-left">
        <li class="zzw-flexible">
          <a class="fa fa-dedent" @click="toggleMenu"></a>
        </li>
      </ul>
      <ul class="zzw-nav zzw-layout-right">
        <li>
          <a>
            <i class="fa fa-user-circle-o"></i>
            <span>{{name}}</span>
          </a>
        </li>
        <li @click="reload">
          <a>
            <i class="fa fa-refresh"></i>
            <span>刷新</span>
          </a>
        </li>
        <li @click="shift">
          <a>
            <i class="fa fa-users"></i>
            <span>交班</span>
          </a>
        </li>
        <li>
          <a @click="quit">
            <i class="fa fa-power-off"></i>
            <span>注销</span>
          </a>
        </li>
      </ul>
    </header>
    <!--侧边菜单-->
    <div class="zzw-side sidebar" id="menu">
      <div class="logo">
        <div class="img-box">
          <img src="../../static/img/logo.png" id="logo" />
        </div>
      </div>
      <div class="side-scroll">
        <ul class="zzw-nav zzw-nav-tree bg-black">
          <li id="menu1">
            <router-link :to="{name:'cashier'}">
              <i class="fa fa-z-receivables-h"></i>
              <span>收款</span>
            </router-link>
          </li>
          <li id="menu2">
            <router-link :to="{name:'order'}">
              <i class="fa fa-z-contract"></i>
              <span>流水</span>
            </router-link>
          </li>
          <li id="menu3">
            <router-link :to="{name:'hotkey'}">
              <i class="fa fa-hand-o-up"></i>
              <span>热键设置</span>
            </router-link>
          </li>
          <li id="menu4">
            <router-link :to="{name:'buttjoint'}">
              <i class="fa fa-link"></i>
              <span>收银对接</span>
            </router-link>
          </li>
          <li id="menu5">
            <router-link :to="{name:'password'}">
              <i class="fa fa-key"></i>
              <span>密码修改</span>
            </router-link>
          </li>
          <li id="menu6">
            <router-link :to="{name:'other'}">
              <i class="fa fa-cog"></i>
              <span>其他设置</span>
            </router-link>
          </li>
        </ul>
      </div>
    </div>
    <!--二维码弹窗-->
    <div class="zzw-popup-wrap" style="display: none">
      <div class="zzw-popup-content" id="qrcode_tip">
        <a class="fa fa-remove" onclick="countdown.stop();hideQrcode();clear();"></a>
        <div class="img-box">
          <img id="qrcode" src="../../static/img/qr-code.png" />
        </div>
        <p id="tip"></p>
        <div style="flex-direction: row">
          <p id="dtime" style="color: red"></p>
        </div>
      </div>
      <div class="zzw-layer-shade"></div>
    </div>
    <router-view />
  </div>
</template>

<script>
import { mapGetters, mapMutations, mapActions, mapState } from "vuex";
import Vue from "vue";
import map from "../utils/map.js";
import keyOfFun from "../utils/hotkey.js";
import Timer from "../utils/timer.js";
export default {
  name: "home",
  data() {
    return {
      name: "test",
      menuVisible: true,
      timer: Timer()
    };
  },
  computed: {
    ...mapGetters(["isFocus", "getKeys"])
  },
  created: function() {
    var w = this;
    window.document.onkeydown = function(event) {
      var e = event || window.event || arguments.callee.caller.arguments[0];
      var key = map.get(e.keyCode);

      console.log("是否拦截:" + w.isFocus);
      if (w.isFocus) {
        return;
      }
      var fun = keyOfFun(w.getKeys, key);
      if (fun == "payPage") {
        w.$router.push("/home");
      } else if (fun == "orderPage") {
        w.$router.push("/order");
      } else if (fun == "passwordPage") {
        w.$router.push("/password");
      } else if (fun == "hotkeyPage") {
        w.$router.push("/hotkey");
      } else if (fun == "quit") {
        w.$router.push("/");
      } else if (fun == "shift") {
        alert("交接班");
        w.timer.start(5, 1);
      }
    };
  },
  methods: {
    toggleMenu: function() {
      this.menuVisible = !this.menuVisible;
    },
    reload: function() {
      console.log("reload");
      location.reload();
    },
    shift: function() {
      console.log("shift");
    },
    quit: function() {
      this.$router.replace("/");
    }
  }
};
</script>
