import Vue from 'vue'
import Router from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import Order from '../views/Order.vue'
import Cashier from '../views/Cashier.vue'
import HotKey from '../views/HotKey.vue'
import ButtJoint from '../views/ButtJoint.vue'
import Password from '../views/Password.vue'
import Other from '../views/Other.vue'
Vue.use(Router)

const router = new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [{
    path: '/',
    name: 'login',
    component: Login
  },
  {
    path: '/home',
    name: 'home',
    component: Home,
    children: [{
      path: '/',
      name: 'cashier',
      component: Cashier
    },
    {
      path: '/order',
      name: 'order',
      component: Order
    },
    {
      path: '/hotkey',
      name: 'hotkey',
      component: HotKey
    },
    {
      path: '/buttjoint',
      name: 'buttjoint',
      component: ButtJoint
    },
    {
      path: '/password',
      name: 'password',
      component: Password
    }, {
      path: '/other',
      name: 'other',
      component: Other
    }
    ]
  }
  ]
})
router.beforeEach((to, from, next) => {
  // console.log(this.$store.state.intercept);
  // if (to.path == from.path) return;
  next();
})
router.onError((e) => {
  console.log(e);
})
export default router;